import React, { useEffect, useState } from 'react';
import Axios from 'axios';
import { HorizontalBar } from 'react-chartjs-2';


export default function AgeGroup() {

    const [graphData, setGraphData] = useState({});

    useEffect(() => {
        //https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/age_group/FeatureServer/0/query?f=json&where=1=1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&groupByFieldsForStatistics=age_categ,sex&outStatistics=[{"statisticType":"count","onStatisticField":"FID","outStatisticFieldName":"value"}]&cacheHint=true
        Axios.get("https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/age_group/FeatureServer/0/query?", {
            params: {
                f: 'json',
                where: '1=1',
                returnGeometry: false,
                spatialRel: 'esriSpatialRelIntersects',
                outFields: '*',
                groupByFieldsForStatistics: 'age_categ,sex',
                cacheHint: true,
                outStatistics: '[{"statisticType":"count","onStatisticField":"FID","outStatisticFieldName":"value"}]'
            }
        }).then(response => {
            let allLabels = [];
            response.data.features.map((f, index) => {
                if (allLabels.indexOf(f.attributes.age_categ) <= -1) {
                    allLabels.push(f.attributes.age_categ);
                }
            });

            const comparator = (a, b) => {
                let firstA = parseInt(a.split(" ")[0]);
                let firstB = parseInt(b.split(" ")[0]);
                return firstA - firstB;
            };

            allLabels.sort(comparator);
            console.log(allLabels);
            
            // const labels = allLabels.filter((label, index, self) => {
            //     return self.indexOf(label) === index;
            // })

            let dataMale = new Array(allLabels.length).fill(0);
            let dataFemale = new Array(allLabels.length).fill(0);
            response.data.features.map(f => {
                const index = allLabels.indexOf(f.attributes.age_categ);
                if (index >= 0) {
                    if (f.attributes.sex === "Male") {
                        dataMale[index] = f.attributes.value;
                    } else {
                        dataFemale[index] = f.attributes.value;
                    }
                }
            });

            const graphData = {
                labels: allLabels,
                datasets: [
                    {
                        label: 'Male',
                        backgroundColor: '#265d82',
                        borderColor: '#265d82',
                        borderWidth: 1,
                        hoverBackgroundColor: '#265d82',
                        hoverBorderColor: '#265d82',
                        data: dataMale
                    },
                    {
                        label: 'Female',
                        backgroundColor: '#c462aa',
                        borderColor: '#c462aa',
                        borderWidth: 1,
                        hoverBackgroundColor: '#c462aa',
                        hoverBorderColor: '#c462aa',
                        data: dataFemale
                    }
                ]
            };

            setGraphData(graphData);
        });
    }, []);

    return (
        <div>
            <h3>Cases by Age Group</h3>
            <HorizontalBar data={graphData} />
        </div>
    );
}