import React, { useEffect, useState, } from 'react';
import { Row, Col } from 'react-bootstrap';
import StatCard from './StatCard.js';
import Axios from 'axios';

function PhilStats() {
    useEffect(() => {
        setIsLoading(true);

        /* Slide_FIG
        Confirmed: https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/slide_fig/FeatureServer/0/query?f=json&where=1=1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&outStatistics=[{"statisticType":"sum","onStatisticField":"confirmed","outStatisticFieldName":"value"}]&cacheHint=true
        Deaths:    https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/slide_fig/FeatureServer/0/query?f=json&where=1=1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&outStatistics=[{"statisticType":"sum","onStatisticField":"deaths","outStatisticFieldName":"value"}]&cacheHint=true
        PUIs:      https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/slide_fig/FeatureServer/0/query?f=json&where=1=1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&outStatistics=[{"statisticType":"sum","onStatisticField":"PUIs","outStatisticFieldName":"value"}]&cacheHint=true
        PUMs:      https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/slide_fig/FeatureServer/0/query?f=json&where=1=1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&outStatistics=[{"statisticType":"sum","onStatisticField":"PUMs","outStatisticFieldName":"value"}]&cacheHint=true
        Recovered: https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/slide_fig/FeatureServer/0/query?f=json&where=1=1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&outStatistics=[{"statisticType":"sum","onStatisticField":"recovered","outStatisticFieldName":"value"}]&cacheHint=true
        Tests:     https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/slide_fig/FeatureServer/0/query?f=json&where=1=1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&outStatistics=[{"statisticType":"sum","onStatisticField":"tests","outStatisticFieldName":"value"}]&cacheHint=true
        */
       
        Axios.get('https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/slide_fig/FeatureServer/0/query?', {
            params: {
                f: 'json',
                where: '1=1',
                returnGeometry: false,
                spatialRel: 'esriSpatialRelIntersects',
                outFields: '*',
                cacheHint: true,
                outStatistics: ''
            }
        }).then(response => {
            response.data.features.map(f => {
                setCases(f.attributes.confirmed);
                setRecovered(f.attributes.recovered);
                setDeaths(f.attributes.deaths);
                setPUIs(f.attributes.PUIs);
                setPUMs(f.attributes.PUMs);
                setTests(f.attributes.tests);
            });
        });
        setIsLoading(false);
    }, []);

    const [cases, setCases] = useState(0);
    const [recovered, setRecovered] = useState(0);
    const [deaths, setDeaths] = useState(0);
    const [puis, setPUIs] = useState(0);
    const [pums, setPUMs] = useState(0);
    const [tests, setTests] = useState(0);
    const [isLoading, setIsLoading] = useState(true);

    return (
        <Row>
            <Col sm={6} md={4}>
                <StatCard title={<span><i className="las la-procedures"></i>Cases</span>} main={<span>{cases}</span>} footer='Total of all cases.' style='bg-info mb-3 text-white' />
            </Col>
            <Col sm={6} md={4}>
                <StatCard title={<span><i className="las la-user"></i>Recovered</span>} main={<span>{recovered}</span>} footer={<span>{((recovered / cases) * 100).toLocaleString(undefined, { maximumFractionDigits: 2 })}% have recovered.</span>} style='bg-success mb-3 text-white' />
            </Col>
            <Col sm={6} md={4}>
                <StatCard title={<span><i className="las la-skull"></i>Deaths</span>} main={<span>{deaths}</span>} footer={<span>{((deaths / cases) * 100).toLocaleString(undefined, { maximumFractionDigits: 2 })}% of all cases died.</span>} style='bg-danger mb-3 text-white' />
            </Col>
            <Col sm={6} md={4}>
                <StatCard title={<span><i className="las la-file-medical-alt"></i>PUIs</span>} main={<span>{puis}</span>} footer='Pt. under investigation.' style='bg-light mb-3' />
            </Col>
            <Col sm={6} md={4}>
                <StatCard title={<span><i className="las la-file-medical-alt"></i>PUMs</span>} main={<span>{pums}</span>} footer='Pt. under monitoring.' style='bg-light mb-3' />
            </Col>
            <Col sm={6} md={4}>
                <StatCard title={<span><i className="las la-microscope"></i>Tests</span>} main={<span>{tests}</span>} footer='Tests conducted.' style='bg-light mb-3' />
            </Col>
        </Row>
    );
}

export default PhilStats;
