import React, { useEffect, useState, Fragment } from 'react';
import { Pie } from 'react-chartjs-2';

function WorldStats() {

    useEffect(() => {
        fetchStats();
    }, []);

    //const [isLoading, setIsLoading] = useState(false);
    const [data, setData] = useState({});
    const [ongoingCases, setOngoingCases] = useState(0);
    const [deaths, setDeaths] = useState(0);
    const [recovered, setRecovered] = useState(0);
    const [total, setTotal] = useState(0);

    const fetchStats = async () => {
        const res = await fetch('https://coronavirus-19-api.herokuapp.com/all');
        const j = await res.json();

        var ongoing = j.cases - (j.deaths + j.recovered);
        const graphData = {
            labels: [
                'Deaths',
                'Recovered',
                'Open Cases'
            ],
            datasets: [{
                data: [j.deaths, j.recovered, ongoing],
                backgroundColor: [
                    '#ff3b3b',
                    '#00b646',
                    '#ffa24c'
                ],
                hoverBackgroundColor: [
                    '#ff3b3b',
                    '#00b646',
                    '#ffa24c'
                ]
            }]
        };

        setOngoingCases(ongoing);
        setDeaths(j.deaths);
        setRecovered(j.recovered);
        setTotal(j.cases);
        setData(graphData);
    }

    return (
        <Fragment>
            <h3>World Total</h3>
            <Pie data={data} />
            <div className='text-center p-3'>
                <span className="text-warning mr-3"><i className="las la-square-full" ></i>: {ongoingCases.toLocaleString(undefined, { maximumFractionDigits: 0 })}</span>
                <span className="text-success mr-3"><i className="las la-square-full"></i>: {recovered.toLocaleString(undefined, { maximumFractionDigits: 0 })}</span>
                <span className="text-danger mr-3"><i className="las la-square-full"></i>: {deaths.toLocaleString(undefined, { maximumFractionDigits: 0 })}</span>
                <span>Total: {total.toLocaleString(undefined, { maximumFractionDigits: 0 })}</span>
            </div>

        </Fragment>
    );
}

export default WorldStats;
