import React, { Fragment, useState } from 'react';
import { Container, Row } from 'react-bootstrap';
import { Map, TileLayer, Marker, Popup } from 'react-leaflet';
import FacilityTable from './FacilityTable';


export default function Facilities() {

    
    const [position, setPosition] = useState([11.577942, 122.671174]);
    const [zoom, setZoom] = useState(5);
    const moveMap = (lat, long) => {
        setPosition([lat, long]);
        setZoom(16);
    }

    //https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/conf_fac_tracking/FeatureServer/0/query?f=json&where=1%3D1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&outSR=102100&resultOffset=0&resultRecordCount=200&cacheHint=true
    return (
        <Fragment>
            <Map className='map-container' center={position} zoom={zoom}>
                <TileLayer
                    attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                <Marker position={position}>
                    <Popup>

                    </Popup>
                </Marker>
            </Map>
            <Container className='mt-3 mb-3'>
                <Row xs={1} className='text-center'>
                    <h1>Facilities Handling COVID-19 Cases</h1>
                    <h4>Click on a facility to view map</h4>
                </Row>
                <Row xs={1}>
                    <FacilityTable handleClick={moveMap}/>
                </Row>
            </Container>
        </Fragment>

    )
}