import React, { Fragment, useState, useEffect } from 'react';
import Axios from 'axios';
import { Table, Badge, Form } from 'react-bootstrap';
import Pagination from './Pagination.js';

export default function FacilityTable(props) {

    const [facilities, setFacilities] = useState([]);
    const [selectedFacilites, setSelectedFacilities] = useState([]); //data to display on table
    const [filteredFacilities, setFilteredFacilities] = useState([]);
    const [numPerPage] = useState(10);

    const [numPages, setNumPages] = useState(1);
    const [currentPage, setCurrentPage] = useState(1);

    useEffect(() => {
        Axios.get('https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/conf_fac_tracking/FeatureServer/0/query?', {
            params: {
                f: 'json',
                where: '1=1',
                returnGeometry: false,
                spatialRel: 'esriSpatialRelIntersects',
                outFields: '*',
                cacheHint: true,
            }
        }).then(response => {
            setFacilities(response.data.features);
            setFilteredFacilities(response.data.features);
            setSelectedFacilities(response.data.features.slice((currentPage * numPerPage) - numPerPage, currentPage * numPerPage));
            setNumPages(Math.ceil(response.data.features.length / numPerPage));
        })
    }, []);

    const handleClick = (selectedPage) => {     
        let selected = filteredFacilities.slice((selectedPage * numPerPage) - numPerPage, selectedPage * numPerPage)
        setSelectedFacilities(selected);
        setCurrentPage(selectedPage);
    }

    const handleFacilityChange = (e) => {
        e.preventDefault();
        let entry = e.target.value;
        let filtered = facilities.filter(f => f.attributes.facility.toLowerCase().includes(entry.toLowerCase()));
        setFilteredFacilities(filtered);
        setSelectedFacilities(filtered.slice(0, numPerPage));
        setNumPages(Math.ceil(filtered.length / numPerPage));
        setCurrentPage(1);
    }

    return (
        <Fragment>
            <Form className='p-3'>
                <Form.Row>
                        <Form.Control type='text' placeholder='Filter facility' onChange={handleFacilityChange} />
                </Form.Row>
            </Form>
            <Table responsive>
                        <thead>
                            <tr>
                                <th>Facility</th>
                            </tr>
                        </thead>
                        <tbody> 
                                {selectedFacilites.map(f => {
                                    f = f.attributes;
                                    return <tr key={f.facility}><td onClick={() => props.handleClick(f.latitude, f.longitude)}>{f.facility}<Badge variant='info'>{f.count_} cases</Badge></td></tr>
                                })}
                        </tbody>
                    </Table>
                    <Pagination numOfPages={numPages} currentPage={currentPage} handleClick={handleClick}/>
        </Fragment>
    );
}